import React from "react"
import styled from "styled-components"

const WrapLayout = styled.div`
  display: grid;
  grid-template-columns: ${props => new Array(props.col).fill("1fr").join(" ")};
  gap: ${props => props.gap || "1rem"};
`
class MyLayoutBase extends React.Component {
  render() {
    return (
      <WrapLayout
        col={this.props.col}
        gap={this.props.gap}
        style={this.props.style}
        className={this.props.className}
      >
        {this.props.children}
      </WrapLayout>
    )
  }
}
MyLayoutBase.defaultProps = {
  col: 4,
}
export default MyLayoutBase
