import React from "react"
import MyLayoutGrid from "./grid"
class MUI_grid_autofit extends React.Component {
  render() {
    return (
      <MyLayoutGrid
        style={{
          ...{
            gridTemplateColumns: `repeat( auto-fill, minmax(${
              this.props.minWidth || "250px"
            }, 1fr) )`,
          },
          ...this.props.style,
        }}
        className={this.props.className}
      >
        {this.props.children}
      </MyLayoutGrid>
    )
  }
}

export default MUI_grid_autofit
